.roadmap-wrapper {
  width: 100%;
  padding: 3rem 1rem;
  background: transparent;
  color: white;
  text-align: center;
}
.status-dot.inprogress {
  width: 20px;
  height: 20px;
  border: 2px solid white;
  border-radius: 50%;
  background-color: transparent;
}

.roadmap-title {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 1rem;
  color: #377a7a;
}

.roadmap-slider-container {
  overflow-x: auto; 
  white-space: nowrap;
  scroll-behavior: smooth;
  padding: 1rem 0 1rem 1rem;
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.roadmap-slider-container::-webkit-scrollbar {
  display: none;
}

.roadmap-slider-track {
  display: flex;
  flex-direction: row; /* <<< EZ a kulcs */
  gap: 1.5rem;
  width: max-content;
}

.roadmap-card.done-shadow {
  box-shadow: 0 0 3px #efcb5d; /* Arany fény */
}
.roadmap-card-wrapper {
  min-width: 250px;
  max-width: 250px;
  flex: 0 0 auto;
}


.roadmap-card {
  padding: 1rem;
  background-color: linear-gradient(to right, #0f0f0f, #1a1a1a);
  max-height: 150px;
  min-height: 150px;
  border-radius: 0.5rem;
  transition: box-shadow 0.3s ease, transform 0.2s ease;
}

.roadmap-card:hover {
  box-shadow: 0 0 3px rgb(255, 215, 0), 0 0 5px #a99c7b;
  transform: translateY(-4px);
}

.roadmap-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 0.75rem;
}

.status-dot {
  width: 24px;
  height: 24px;
  border-radius: 9999px;
}

.status-dot.yellow {
  background-color: #facc15;
}

.status-dot.gray {
  background-color: #9ca3af;
}

.roadmap-month {
  font-size: 0.875rem;
  color: gold;
  font-weight: 600;
  margin-bottom: 0.25rem;
}

.roadmap-title-item {
  font-size: 1.2rem;
  font-weight: bold;
  color: #ffffff;
}

.roadmap-description {
  font-size: 0.875rem;
  color: #cbd5e1;
  margin-top: 0.25rem;
    word-break: break-word;     /* ← ez engedi megtörni hosszú szavakat is */
    overflow-wrap: break-word;
    white-space: normal;
    text-align: center;
  }

.roadmap-dots {
  display: flex;
  justify-content: center;
  gap: 8px;
  margin-top: 1.5rem;
}

.dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: #333;
  border: none;
  transition: background 0.3s ease;
  cursor: pointer;
}

.dot.active {
  background-color: #00e1d4;
}
.status-done {
  display: flex;
  align-items: center;
  gap: 6px;
  justify-content: center;
  color: #efcb5d;
  font-weight: bold;
  font-size: 0.75rem;
  text-transform: uppercase;
}

.status-inprogress {
  display: flex;
  align-items: center;
  gap: 6px;
  justify-content: center;
  color: white;
  font-weight: bold;
  font-size: 0.75rem;
  text-transform: uppercase;
}

.inprogress-label {
  color: white;
}

.done-label {
  color: #efcb5d;   
}